import React from "react";

import { Row } from "react-bootstrap";
import Labor from "../../components/Payroll"


class index extends React.Component {

    render() {
        return (
            <main>
                <Row>
                    <br />
                    <Labor timeCards={this.props.timeCards.filteredTimeCards} states={this.props}/>
                    <br />
                    <br />
                </Row>
            </main>
        )
    }
}

export default index;