import React from "react";
// import moment from "moment";
import { Line } from 'react-chartjs-2';
import Chart from 'chart.js/auto';


function OTEmployee(props) {
    let chartData = {
        labels: [],
        datasets: [
            {
                id: 1,
                label: "OT",
                fillColor: "rgba(220,220,20,0.2)",
                strokeColor: "rgba(220,220,20,1)",
                pointColor: "rgba(220,220,20,1)",
                pointStrokeColor: "#fff",
                pointHighlightFill: "#fff",
                pointHighlightStroke: "rgba(220,220,20,1)",
                data: []
            },
            {
                id: 2,
                label: "DT",
                fillColor: "rgba(220,20,20,0.2)",
                strokeColor: "rgba(220,20,20,1)",
                pointColor: "rgba(220,20,20,1)",
                pointStrokeColor: "#fff",
                pointHighlightFill: "#fff",
                pointHighlightStroke: "rgba(220,20,20,1)",
                data: []
            }
        ]
    };
    const chartOptions = {
        ///Boolean - Whether grid lines are shown across the chart
        scaleShowGridLines : true,
        //String - Colour of the grid lines
        scaleGridLineColor : "rgba(0,0,0,.05)",
        //Number - Width of the grid lines
        scaleGridLineWidth : 1,
        //Boolean - Whether to show horizontal lines (except X axis)
        scaleShowHorizontalLines: true,
        //Boolean - Whether to show vertical lines (except Y axis)
        scaleShowVerticalLines: true,
        //Boolean - Whether the line is curved between points
        bezierCurve : true,
        //Number - Tension of the bezier curve between points
        bezierCurveTension : 0.4,
        //Boolean - Whether to show a dot for each point
        pointDot : true,
        //Number - Radius of each point dot in pixels
        pointDotRadius : 4,
        //Number - Pixel width of point dot stroke
        pointDotStrokeWidth : 1,
        //Number - amount extra to add to the radius to cater for hit detection outside the drawn point
        pointHitDetectionRadius : 20,
        //Boolean - Whether to show a stroke for datasets
        datasetStroke : true,
        //Number - Pixel width of dataset stroke
        datasetStrokeWidth : 2,
        //Boolean - Whether to fill the dataset with a colour
        datasetFill : true,
        //String - A legend template
        //Boolean - Whether to horizontally center the label and point dot inside the grid
        offsetGridLines : false
    };
    if (props.timeCards.length === 0) {
        return(
            <main>
                <Line width="400" height="250" data={chartData} options={chartOptions}/>
            </main>
        )
    }
    else {
        let OTChartData = {};
        Object.keys(props.timeCards)?.map((desc, descKey) => {
            // if (props.timeCards[desc]["Has OT"]) {
                let date = props.timeCards[desc]["Formatted Date"]
                if(OTChartData[date] === undefined && date !== "Invalid date") {
                    OTChartData[date] = {"TOT": 0, "OT": 0, "DT": 0}
                }
                // }
                // else if (OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')] === undefined) {
                //     OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')] = {"TOT": 0, "OT": 0, "DT": 0}
                // }
                if(date !== "Invalid date") {
                    OTChartData[date]["TOT"] += props.timeCards[desc]["Total Hours"]
                    OTChartData[date]["OT"] += props.timeCards[desc]["1.5x Hrs"]
                    OTChartData[date]["DT"] += props.timeCards[desc]["2x Hours"]
                    OTChartData[date]["DT"] += props.timeCards[desc]["2x Hours"]
                    OTChartData[date]["1.5 Time"] += (props.timeCards[desc]["1.5x Hours"]*60)
                    let labels = [];
                    let OTDataPoints = []
                    let DTDataPoints = []
                    const ordered = Object.keys(OTChartData).sort().reduce(
                        (obj, key) => {
                            obj[key] = OTChartData[key];
                            return obj;
                        },
                        {}
                    );
                    Object.keys(ordered).forEach(data => {
                        labels.push(data)
                        OTDataPoints.push(ordered[data]["OT"])
                        DTDataPoints.push(ordered[data]["DT"])
                        // console.log(labels, dataPoints)
                    })
                    chartData.labels = labels;
                    chartData.datasets[0]["data"] = OTDataPoints
                    chartData.datasets[1]["data"] = DTDataPoints
                    // console.log(labels)
                }
            // }
        })
        return (
            <main>
                <Line data={chartData} options={chartOptions} width="400" height="250" redraw />
            </main>
        );
    }
}

export default OTEmployee;