import React from "react";
// import moment from "moment";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';



function OTEmployee(props) {
    function OTLevel(OT) {
        let level = "none";
        if (OT < 0.2) {
            level = "minimal"
        }
        else if (OT < .4) {
            level = "medium"
        }
        else if (OT < .8) {
            level = "high"
        }
        else {
            level = "danger"
        }
        return level;
    }
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>OT By Date</h1>
                <p>No shifts that had OT</p>
            </main>
        )
    }
    else {
        let OTData = {
        };
        Object.keys(props.timeCards)?.map((desc, descKey) => {
            if (props.timeCards[desc]["Has OT"]) {
                if(OTData[props.timeCards[desc]["Name"]] === undefined) {
                    OTData[props.timeCards[desc]["Name"]] = {"Type of Staff": props.timeCards[desc]["Role Type"],"Name": props.timeCards[desc]["Name"],"dates":{}, "Role": props.timeCards[desc]["Role"]}
                    OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')] = {"TOT": 0, "OT": 0, "DT": 0}
                }
                else if (OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')] === undefined) {
                    OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')] = {"TOT": 0, "OT": 0, "DT": 0}
                }
                OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')]["TOT"] += props.timeCards[desc]["Total Hours"]
                OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')]["OT"] += props.timeCards[desc]["1.5x Hrs"]
                OTData[props.timeCards[desc]["Name"]]["dates"][props.timeCards[desc]["Start Time"].format('L')]["DT"] += props.timeCards[desc]["2x Hours"]
            }
        })
        return (
            <main>
                <h1>OT By Date</h1>
                    <Row>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                            <th>Type of Staff</th>
                            <th>Role</th>
                            <th>Name</th>
                            <th>Date</th>
                            <th>Total Hours</th>
                            <th>OT Hours</th>
                            <th>DT Hours</th>
                            <th>1.5 Minutes</th>
                            </tr>
                        </thead>
                        <tbody>
                        {
                        Object.keys(OTData)?.map((desc, descKey) => {
                            return(
                                <>
                                <tr key={descKey} >
                                <td>{OTData[desc]["Type of Staff"]}</td>
                                <td>{OTData[desc]["Role"]}</td>
                                <td>{OTData[desc]["Name"]}</td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                {/* <td>{OTData[desc]["Date"]}</td> */}
                                {/* <td>{OTData[desc]["TOT"]}</td> */}
                                {/* <td>{OTData[desc]["OT"]}</td> */}
                                {/* <td>{OTData[desc]["DT"]}</td> */}
                                </tr>
                                {Object.keys(OTData[desc].dates)?.map((dates, datesKey) => {
                                    console.log(OTData[desc])
                                    return (
                                        <>
                                        <tr key={descKey} >
                                            <td></td>
                                            <td></td>
                                            <td></td>
                                            <td>{dates}</td>
                                            <td>{OTData[desc].dates[dates]["TOT"]}</td>
                                            <td className={
                                                OTData[desc].dates[dates]["OT"]>0 ? (
                                                    OTLevel(OTData[desc].dates[dates]["OT"])
                                                ) : (
                                                    ""
                                                )
                                            }>{OTData[desc].dates[dates]["OT"]}</td>
                                            <td className={
                                                OTData[desc].dates[dates]["OT"]>0 ? (
                                                    OTLevel(OTData[desc].dates[dates]["OT"])
                                                ) : (
                                                    ""
                                                )
                                            }>{OTData[desc].dates[dates]["DT"]}</td>
                                            <td className={
                                                OTData[desc].dates[dates]["OT"]>0 ? (
                                                    OTLevel(OTData[desc].dates[dates]["OT"])
                                                ) : (
                                                    ""
                                                )
                                            }>{OTData[desc].dates[dates]["OT"]*60} Minutes</td>
                                            </tr>
                                        </>
                                    )
                                })}
                                </>
                            )
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default OTEmployee;