import React from "react";
import moment from "moment";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';



function NoShift(props) {
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>No Shifts</h1>
                <p>No users had no shifts</p>
            </main>
        )
    }
    else {
        return (
            <main>
                <h1>No Shifts</h1>
                    <Row>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                            <th>Role</th>
                            <th>Name</th>
                            <th>Date</th>
                            <th>Total Hours</th>
                            </tr>
                        </thead>
                        <tbody>
                        {Object.keys(props.timeCards)?.map((desc, descKey) => {
                            if (props.timeCards[desc]["No Shift"]) {
                                return(
                                    <tr key={descKey} >
                                    <td>{props.timeCards[desc]["Role"]}</td>
                                    <td>{props.timeCards[desc]["Name"]}</td>
                                    <td>{props.timeCards[desc]["Date"]}</td>
                                    <td>{props.timeCards[desc]["Total Hours"]}</td>
                                    </tr>
                                )
                            }
                            else {
                                return(<></>)
                            }
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default NoShift;