import React from "react";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import { faDownload } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


function Payroll(props) {
    var dates = props.states.roles.dates
    let payrollTimeCards = {}
    var table = {}
    let tableHeader = []
    let csv = []
    function loadPayrollData() {
        Object.keys(props.timeCards).forEach(timeCard => {
            let formattedDate = [props.timeCards[timeCard]["Formatted Date"]]
            let empID = props.timeCards[timeCard]["Employee ID"]
            if (empID === "") {
                empID = "0"
            }
            let role = [props.timeCards[timeCard]["Role"]]
            let shiftType = empID + "-" + role
            if (payrollTimeCards[shiftType] === undefined) {
                payrollTimeCards[shiftType] = {Role:role,Name:props.timeCards[timeCard]["Name"], ID:empID}
            }
            if (payrollTimeCards[shiftType][formattedDate] === undefined) {
                payrollTimeCards[shiftType][formattedDate] = []
            }
            payrollTimeCards[shiftType][formattedDate].push(props.timeCards[timeCard]);
        })
        // console.log(payrollTimeCards)
        tableHeader = ["Role", "Name"]
        dates.forEach(date => {
            tableHeader.push(date)
        })
        Object.keys(payrollTimeCards).forEach(empID => {
            let employeeShifts = {}
            employeeShifts["Role"] = payrollTimeCards[empID]["Role"];
            employeeShifts["ID"] = payrollTimeCards[empID]["ID"]
            employeeShifts["Name"] = payrollTimeCards[empID]["Name"]
            dates.forEach(date => {
                let currentEmployeeShift = payrollTimeCards[empID][date]
                if (currentEmployeeShift === undefined) {
                    employeeShifts[date] = {times: "Off", breaks: 'No breaks', total : 0
                    };
                }
                else {
                    currentEmployeeShift.forEach(shift => {
                        let info = {
                            times: shift["Formatted Start"] + " - " + shift["Formatted End"],
                            breaks: shift["Formatted Break Times"],
                            total: shift["Total Hours"]
                        }
                        if (shift["Formatted Break Times"] === "") {
                            info['breaks'] = "No Break"
                        }
                        if (shift["Formatted Start"] === "No-Shift") {
                            info['times'] = "No Shift"
                        }
                        employeeShifts[date] = info
                    })
                }
            })
            table[empID] = (employeeShifts)
        })
        csv.push(tableHeader.join(","));
        Object.keys(table).forEach(emp => {
            let empDate = [table[emp]["Role"],table[emp]["ID"],table[emp]["Name"]]
            // dates.forEach(date => {
            //     let totalHrs = 0;
            //     table[emp][date].forEach(dateShift => {
            //         totalHrs += dateShift
            //     })
            //     empDate.push(Math.round(totalHrs*100)/100)
            // })
            csv.push(empDate.join(','))
        })
    }
    function downloadTableAsCSV(obj, separator = ',') {
        csv = csv.join('\r\n')
        var filename = 'export_payroll_' + new Date().toLocaleDateString() + '.csv';
        var link = document.createElement('a');
        link.style.display = 'none';
        link.setAttribute('target', '_blank');
        link.setAttribute('href', 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv));
        link.setAttribute('download', filename);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>Payroll Report</h1>
                <p>No payroll data available</p>
            </main>
        )
    }
    else {
        loadPayrollData()
        return (
            <main>
                <h1>Payroll Report <Button id='upload' variant="success" onClick={(e) => downloadTableAsCSV(props.timeCards)}><FontAwesomeIcon icon={faDownload} /> Download Table</Button></h1>
                    <Row>
                    <Table striped bordered hover id="table" size="sm">
                        <thead>
                            <tr>
                                {Object.keys(tableHeader)?.map((desc, descKey) => {
                                    return(
                                        <th key={descKey}>{tableHeader[desc]}</th>
                                    )
                                })}
                            </tr>
                        </thead>
                        <tbody>
                        {Object.keys(table)?.map((emp, descKey) => {
                            return (
                                <>
                                    <tr key={descKey}>
                                        <td>{table[emp]["Role"]}</td>
                                        <td>{table[emp]["Name"]}</td>
                                        {
                                            Object.keys(dates)?.map((date, descKey) => {
                                                return(
                                                    <td>
                                                        {table[emp][dates[date]]['times']}
                                                        <hr />
                                                        {table[emp][dates[date]]['breaks']}
                                                        <hr />
                                                        {table[emp][dates[date]]['total']}
                                                    </td>
                                                )
                                            })
                                        }
                                    </tr>
                                </>
                            )
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default Payroll;