import React from "react";
// import moment from "moment";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';



function OTDepartment(props) {
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>OT By Deparment</h1>
                <p>No shifts that had OT</p>
            </main>
        )
    }
    else {
        let OTData = {
        };
        Object.keys(props.timeCards)?.map((desc, descKey) => {
            if (props.timeCards[desc]["Has OT"]) {
                if(OTData[props.timeCards[desc]["Role"]] === undefined) {
                    OTData[props.timeCards[desc]["Role"]] = {"Role Type": props.timeCards[desc]["Role Type"], "Role": props.timeCards[desc]["Role"], "TOT": 0, "OT": 0, "DT": 0}
                }
                OTData[props.timeCards[desc]["Role"]]["TOT"] += props.timeCards[desc]["Total Hours"]
                OTData[props.timeCards[desc]["Role"]]["OT"] += props.timeCards[desc]["1.5x Hrs"]
                OTData[props.timeCards[desc]["Role"]]["DT"] += props.timeCards[desc]["2x Hours"]
            }
        })
        return (
            <main>
                <h1>OT By Department</h1>
                    <Row>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                            <th>Type of Staff</th>
                            <th>Role</th>
                            <th>Total Hours</th>
                            <th>OT Hours</th>
                            <th>DT Hours</th>
                            <th>1.5 Minutes</th>
                            </tr>
                        </thead>
                        <tbody>
                        {
                        Object.keys(OTData)?.map((desc, descKey) => {
                            return(
                                <tr key={descKey} >
                                <td>{OTData[desc]["Role Type"]}</td>
                                <td>{OTData[desc]["Role"]}</td>
                                <td>{OTData[desc]["TOT"]}</td>
                                <td>{OTData[desc]["OT"]}</td>
                                <td>{OTData[desc]["DT"]}</td>
                                <td>{OTData[desc]["OT"]*60} Minutes</td>
                                </tr>
                            )
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default OTDepartment;