import React from "react";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import { faDownload } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './index.css';
import moment from "moment";
const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
})
// Credits https://stackoverflow.com/questions/15547198/export-html-table-to-csv-using-vanilla-javascript

function Labor(props) {
    function downloadTableAsCSV(obj, separator = ',') {
        var fields = [
            "Date",
            "Employee ID",
            "Name",
            "Role",
            "Total Hours",
            "OT Hours",
            "DT Hours",
            "Start Time",
            "End Time",
            "Break Times",
            "Break Hours",
            "Total Tips",
            "Net Sales",
        ].join(",")
        var csv = []
        Object.keys(obj).forEach(timeCard => {
            csv.push([
                obj[timeCard]["Formatted Date"],
                obj[timeCard]["Employee ID"],
                obj[timeCard]["Name"],
                obj[timeCard]["Role"],
                obj[timeCard]["Total Hours"],
                obj[timeCard]["1.5x Hrs"],
                obj[timeCard]["2x Hours"],
                obj[timeCard]["Formatted Start"],
                obj[timeCard]["Formatted End"],
                obj[timeCard]["Formatted Break Times"],
                obj[timeCard]["Break Hrs"],
                obj[timeCard]["Total Tips"],
                obj[timeCard]["Net Sales"]
            ].join(','))
        })
        csv.unshift(fields)
        csv = csv.join('\r\n')
        var filename = 'export_labor_' + new Date().toLocaleDateString() + '.csv';
        var link = document.createElement('a');
        link.style.display = 'none';
        link.setAttribute('target', '_blank');
        link.setAttribute('href', 'data:text/csv;charset=utf-8,' + encodeURIComponent(csv));
        link.setAttribute('download', filename);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>Labor Report</h1>
                <p>No labor data available</p>
            </main>
        )
    }
    else {
        return (
            <main>
                <h1>Labor Report <Button variant="success" onClick={(e) => downloadTableAsCSV(props.timeCards)}><FontAwesomeIcon icon={faDownload} /> Download Table</Button></h1>
                    <Row>
                    <Table striped bordered hover id="table" size="sm">
                        <thead>
                            <tr>
                                <th>Date</th>
                                <th>Employee ID</th>
                                <th>Name</th>
                                <th>Role</th>
                                <th>Total Hours</th>
                                <th>Total OT Hours</th>
                                <th>Start Time</th>
                                <th>End Time</th>
                                <th>Break Times</th>
                                <th>Break Hours</th>
                                <th>Total Tips</th>
                                <th>Net Sales</th>
                            </tr>
                        </thead>
                        <tbody>
                        {Object.keys(props.timeCards)?.map((desc, descKey) => {
                            return(
                                <tr key={descKey} >
                                <td>{ moment(props.timeCards[desc]["Date"]).format('L')}</td>
                                    <td>{props.timeCards[desc]["Employee ID"]}</td>
                                    <td>{props.timeCards[desc]["Name"]}</td>
                                    <td>{props.timeCards[desc]["Role"]}</td>
                                    <td
                                    title={[
                                        props.timeCards[desc]["Short Shift"] ? (
                                            "Shift was shorter than 2 Hours"
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["No Shift"] ? (
                                            "No shift was registered"
                                        ) : (
                                            ""
                                        )
                                    ].join('\r\n')}
                                    className={[
                                        props.timeCards[desc]["Short Shift"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["No Shift"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        )
                                    ].join(' ')}
                                    >{props.timeCards[desc]["Total Hours"]}</td>
                                    {/* OT */}
                                    <td
                                    title={
                                        props.timeCards[desc]["Has OT"] ? (
                                            "Employee has OT"
                                        ) : (
                                            ""
                                        )
                                    }
                                    className={
                                        props.timeCards[desc]["Has OT"] ? (
                                            props.timeCards[desc]["OT Level"]
                                        ) : (
                                            ""
                                        )
                                    }
                                    >{props.timeCards[desc]["Total OT Hours"]}</td>
                                    {/* Formatted Start */}
                                    <td>{props.timeCards[desc]["Formatted Start"]}</td>
                                    <td
                                    title={
                                        props.timeCards[desc]["Forgot Clock Out"] ? (
                                            "No clock out time was registered"
                                        ) : (
                                            ""
                                        )
                                    }
                                    className={
                                        props.timeCards[desc]["Forgot Clock Out"] ? (
                                            "forgot-clock-out"
                                        ) : (
                                            ""
                                        )
                                    }
                                    >{props.timeCards[desc]["Formatted End"]}</td>
                                    <td
                                    title={
                                        props.timeCards[desc]["Late Break"] ? (
                                            "Employee took their break later than needed\r\n" +
                                            "Break needed by: " + props.timeCards[desc]["Break Needed By"].format('LT')
                                        ) : (
                                            ""
                                        )
                                    }
                                    className={
                                        props.timeCards[desc]["Late Break"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        )
                                    }>{props.timeCards[desc]["Formatted Break Times"]}</td>
                                    <td
                                    title={[
                                        props.timeCards[desc]["No Break"] ? (
                                            "No break\r\n" +
                                            "Break needed by: " + props.timeCards[desc]["Break Needed By"].format('LT')
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["Short Break"] ? (
                                            "Break was shorter than 30 minutes"
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["Long Break"] ? (
                                            "Break was longer than 40 minutes"
                                        ) : (
                                            ""
                                        )
                                    ].join('\r\n')}
                                    className={[
                                        props.timeCards[desc]["No Break"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["Short Break"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        ),
                                        props.timeCards[desc]["Long Break"] ? (
                                            "bg-warning"
                                        ) : (
                                            ""
                                        )
                                    ].join(' ')
                                    }>{props.timeCards[desc]["Break Hrs"]}</td>
                                    <td>{formatter.format(props.timeCards[desc]["Total Tips"])}</td>
                                    <td>{formatter.format(props.timeCards[desc]["Net Sales"])}</td>
                                </tr>
                            )
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default Labor;