import React from "react";

import { Row } from "react-bootstrap";
import ForgotClock from "../../components/ForgotClock"
import LateBreak from "../../components/LateBreak"
import NoBreak from "../../components/NoBreak"
import ShortShift from "../../components/ShortShift"
import NoShift from "../../components/NoShift"
import LongBreak from "../../components/LongBreak"
import ShortBreak from "../../components/ShortBreak"


class index extends React.Component {
    render() {
        return (
            <main>
                <Row>
                    <br />
                    <ForgotClock timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <LateBreak timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <NoBreak timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <ShortShift timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <NoShift timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <LongBreak timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <ShortBreak timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                </Row>
            </main>
        )
    }
}

export default index;