import React from "react";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';
import moment from "moment";



function LateBreak(props) {
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>Late Break</h1>
                <p>No user had a late break</p>
            </main>
        )
    }
    else {
        return (
            <main>
            <h1>Late Break</h1>
                        <Row>
                        <Table striped bordered hover size="sm">
                            <thead>
                                <tr>
                                <th>Role</th>
                                <th>Name</th>
                                <th>Date</th>
                                <th>Total Hours</th>
                                <th>Break Hours</th>
                                <th>Start Time</th>
                                <th>End Time</th>
                                <th>6 Hours</th>
                                <th>Break Needed By</th>
                                <th>Break Times</th>
                                </tr>
                            </thead>
                            <tbody>
                            {Object.keys(props.timeCards)?.map((desc, descKey) => {
                                if (props.timeCards[desc]["Late Break"]) {
                                    return(
                                        <tr key={descKey} >
                                            <td>{props.timeCards[desc]["Role"]}</td>
                                            <td>{props.timeCards[desc]["Name"]}</td>
                                            <td>{props.timeCards[desc]["Start Time"].format('L')}</td>
                                            <td>{props.timeCards[desc]["Total Hours"]}</td>
                                            <td>{props.timeCards[desc]["Break Hrs"]}</td>
                                            <td>{props.timeCards[desc]["Start Time"].format('LT')}</td>
                                            <td>{props.timeCards[desc]["End Time"].format('LT')}</td>
                                            <td>{moment(props.timeCards[desc]["Start Time"]).add(6, 'h').format('LT')}</td>
                                            <td>{moment(props.timeCards[desc]["Start Time"]).add(5, 'h').format('LT')}</td>
                                            <td>{props.timeCards[desc]["Break Started"] + " - " + props.timeCards[desc]["Break Ended"]}</td>
                                        </tr>
                                    )
                                }
                                else {
                                    return(<></>);
                                }
                            })}
                        </tbody>
                        </Table>
                    </Row>
            </main>
        );
    }
}

export default LateBreak;