// import logo from './logo.svg';
import {
  Routes,
  Route,
  HashRouter
} from "react-router-dom";
import React from "react";
import './App.css';
import Nav from "./components/Nav";
import Home from "./pages/home";
import Labor from "./pages/Labor";
import { Card } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { Col } from "react-bootstrap";
import { Form } from "react-bootstrap";
import Violations from "./pages/Violations";
import OT from "./pages/OT";
import Payroll from "./pages/Payroll";
import Schedule from "./pages/Schedule";
import NotFound from "./pages/NotFound";
import moment from "moment";
import * as csv from "csvtojson";


class App extends React.Component {
  state = {
      timeCards: [],
      filteredTimeCards: [],
      dates: [],
      roles: [],
      names: [],
      empIDS: [],
      roleTypes: [],
      date: "all",
      name: "all",
      role: "all",
      empID: "all",
      roleType: "all"
  }
  componentDidMount() {
  }
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }
  updateFilter() {
    let filteredTimeCards = []
    Object.keys(this.state.timeCards).map((desc, descKey) => {
      if (
          (this.state.date === this.state.timeCards[desc]["Formatted Date"] || this.state.date === "all") &&
          (this.state.role === this.state.timeCards[desc]["Role"] || this.state.role === "all") &&
          (this.state.name === this.state.timeCards[desc]["Name"] || this.state.name === "all") &&
          (this.state.empID === this.state.timeCards[desc]["Employee ID"] || this.state.empID === "all") &&
          (this.state.roleType === this.state.timeCards[desc]["Role Type"] || this.state.roleType === "all")
          ) {
            filteredTimeCards.push(this.state.timeCards[desc])
          }
        return 0;
    })
    this.setState({filteredTimeCards: filteredTimeCards})
  }
  handleChange(event) {
    if(event.target.id === "date") {
        this.setState({date: event.target.value}, () => {
          this.updateFilter()
        })
    }
    if(event.target.id === "name") {
        this.setState({name: event.target.value}, () => {
          this.updateFilter()
        })
    }
    if(event.target.id === "role") {
        this.setState({role: event.target.value}, () => {
          this.updateFilter()
        })
    }
    if(event.target.id === "empID") {
        this.setState({empID: event.target.value}, () => {
          this.updateFilter()
        })
    }
    if(event.target.id === "roleType") {
        this.setState({roleType: event.target.value}, () => {
          this.updateFilter()
        })
    }
  }
  showFile = async (e) => {
      e.preventDefault()
      const reader = new FileReader()
      reader.onload = async (e) => {
          csv({
              noheader:true,
              output: "csv"
          }).fromString(e.target.result)
          .then((jsonObj)=>{
              let timeCardsAvailable = false;
              let timeCards = [];
              let dates = [];
              let roles = [];
              let names = [];
              let empIDS = [];
              let roleTypes = [];
              jsonObj.forEach(arr => {
                  if(timeCardsAvailable === true && arr.length !== 0 && !arr[5].includes("Total") && arr[0]!=="Employee ID") {
                      let timeCard = {
                          "Employee ID": arr[0],
                              "Last Name": arr[1],
                              "First Name": arr[2],
                              "Role": arr[3],
                              "Date": arr[4],
                              "ShiftStart": arr[5],
                              "ShiftEnd": arr[6],
                              "Break Times": arr[7],
                              "Reg Hrs": parseFloat(arr[8]),
                              "Break Hrs": parseFloat(arr[9]),
                              "1.5x Hrs": parseFloat(arr[10]),
                              "2x Hours": parseFloat(arr[11]),
                              "PayPerHour": parseFloat(arr[12]),
                              "Blended Pay/Hr": parseFloat(arr[13]),
                              "Total Pay": parseFloat(arr[14]),
                              "Net Sales": parseFloat(arr[15]),
                              "Non Revenue Receipts": arr[16],
                              "Net Sales/Hr": arr[17],
                              "Auto Grat": parseFloat(arr[18]),
                              "Cash Tips": parseFloat(arr[19]),
                              "Credit Tips": parseFloat(arr[20]),
                              "Online Order Tips": parseFloat(arr[21]),
                              "Voucher Tips": parseFloat(arr[22]),
                              "Gift Card Tips": parseFloat(arr[23]),
                              "House Account Tips": parseFloat(arr[24]),
                              "Event Tips": parseFloat(arr[25]),
                              "Other Tips": parseFloat(arr[26]),
                              "Total Tender Tips": parseFloat(arr[27]),
                              "Total Tips": parseFloat(arr[28]),
                              "Declared Tips": parseFloat(arr[29]),
                              "Cash Sales": parseFloat(arr[30]),
                              "Card Sales": parseFloat(arr[31]),
                              "Online Order Sales": parseFloat(arr[32]),
                              "Voucher Sales": parseFloat(arr[33]),
                              "Gift Card Sales": parseFloat(arr[34]),
                              "House Account Sales": parseFloat(arr[35]),
                              "Event Sales": parseFloat(arr[36]),
                              "Other Sales": parseFloat(arr[37]),
                              "Total Tender Sales": parseFloat(arr[38]),
                              "Shift": "",
                              "Late Break": false,
                              "Short Break": false,
                              "Break Started": "",
                              "Break Ended": "",
                              "AM Hours": "",
                              "PM Hours": "",
                              "OT Time Length": "",
                              "DT Time Length": "",
                      }
                      timeCard["Name"] = timeCard["First Name"] + " " + timeCard["Last Name"]
                      timeCard["Total Hours"] = timeCard["Reg Hrs"] + timeCard["1.5x Hrs"] + timeCard["2x Hours"]
                      timeCard["Tip %"] = timeCard["Total Tips"]/timeCard["Net Sales"]
                      timeCard["Formatted Start"] = "No-Shift"
                      timeCard["Formatted End"] = "No-Shift"
                      if (timeCard["ShiftEnd"] !== "No-Shift") {
                        timeCard["End Time"] = moment(timeCard["ShiftEnd"])
                        timeCard["Formatted End"] = timeCard["End Time"].format('LT');
                        timeCard["Forgot Clock Out"] = timeCard["End Time"].format("LT") === "11:56 PM";
                      }
                      if (timeCard["ShiftStart"] !== "No-Shift") {
                        timeCard["Start Time"] = moment(timeCard["ShiftStart"])
                        timeCard["Formatted Start"] = timeCard["Start Time"].format('LT');
                      }
                      timeCard["Break Needed By"] = moment(timeCard["ShiftStart"]).add(6, 'h');
                      timeCard["Formatted Break Times"] = "";
                      if (timeCard["Break Times"] !== "") {
                          const breakTimes = timeCard["Break Times"].split(" - ");
                          breakTimes[0] = breakTimes[0].substr(breakTimes[0].length - 8)
                          breakTimes[1] = breakTimes[1].substr(breakTimes[1].length - 8)
                          timeCard["Break Started"] = moment(timeCard["Date"] + " " + breakTimes[0]).format('LT');
                          timeCard["Break Ended"] = moment(timeCard["Date"] + " " + breakTimes[1]).format('LT');
                          timeCard["Formatted Break Times"] = timeCard["Break Started"] + " - " + timeCard["Break Ended"];
                          timeCard["Late Break"] = timeCard["Break Needed By"].isBefore(moment(timeCard["Date"] + " " + breakTimes[0]).utc());
                          timeCard["Short Break"] = timeCard["Break Hrs"] < .5;
                          timeCard["Long Break"] = timeCard["Break Hrs"] > .66;
                      }
                      timeCard["DOW"] = moment(timeCard["ShiftEnd"]).format('dddd');
                      timeCard["Short Shift"] = timeCard["Total Hours"] < 2;
                      timeCard["No Shift"] = timeCard["ShiftStart"] === "No-Shift";
                      timeCard["No Break"] = (timeCard["Total Hours"] > 6 && timeCard["Break Hrs"] < 0.5) || (timeCard["Total Hours"] > 10 && timeCard["Break Hrs"] < 1.0) ;
                      let totalOT = timeCard["1.5x Hrs"] + timeCard["2x Hours"];
                      timeCard["Total OT Hours"] = totalOT;
                      timeCard["Has OT"] = totalOT > 0;
                      timeCard["OT Level"] = "none";
                      if (totalOT < 0.2) {
                        timeCard["OT Level"] = "minimal"
                      }
                      else if (totalOT < .4) {
                        timeCard["OT Level"] = "medium"
                      }
                      else if (totalOT < .8) {
                        timeCard["OT Level"] = "high"
                      }
                      else {
                        timeCard["OT Level"] = "danger"
                      }
                      timeCard["Formatted Date"] = moment(timeCard["Date"]).format('L');

                      switch (timeCard["Role"]) {
                        case 'Bartender':
                            timeCard["Role Type"] = "Service"
                            break;
                        case 'Server':
                            timeCard["Role Type"] = "Service"
                            break;
                        case 'Busser':
                            timeCard["Role Type"] = "FOH"
                            break;
                        case 'Expo/Food Runner':
                            timeCard["Role Type"] = "FOH"
                            break;
                        case 'Host':
                            timeCard["Role Type"] = "FOH"
                            break;
                        case 'Togo':
                            timeCard["TRole Type"] = "FOH"
                            break;
                        case 'Cook':
                            timeCard["Role Type"] = "HOH"
                            break;
                        case 'Dishwasher':
                            timeCard["Role Type"] = "HOH"
                            break;
                        case 'Prep':
                            timeCard["Role Type"] = "HOH"
                            break;
                        case 'Manager':
                            timeCard["Role Type"] = "MGMT"
                            break;
                        default:
                            timeCard["Role Type"] = "VOID"
                            break;
                    }
                      timeCards.push(timeCard);
                      if(dates.indexOf(timeCard["Formatted Date"]) === -1) {
                          dates.push(timeCard["Formatted Date"]);
                      }
                      if(roles.indexOf(timeCard["Role"]) === -1) {
                        roles.push(timeCard["Role"]);
                      }
                      if(names.indexOf(timeCard["Name"]) === -1) {
                        names.push(timeCard["Name"]);
                      }
                      if(empIDS.indexOf(timeCard["Employee ID"]) === -1) {
                        empIDS.push(timeCard["Employee ID"]);
                      }
                      if(roleTypes.indexOf(timeCard["Role Type"]) === -1) {
                        roleTypes.push(timeCard["Role Type"]);
                      }
                  }
                  if (arr[0] === "Timecards") {
                      timeCardsAvailable = true;
                  }
              })
              // let sortedDates = dates.sort((a, b) => a.valueOf() - b.valueOf())
              this.setState({timeCards: timeCards})
              this.setState({filteredTimeCards: timeCards})
              this.setState({dates: dates.sort()})
              this.setState({names: names.sort()})
              this.setState({roles: roles.sort()})
              this.setState({empIDS: empIDS.sort()})
              this.setState({roleTypes: roleTypes.sort()})
          })
      };
      reader.readAsText(e.target.files[0])
  }
  sortFunction(col){
      let sortedObj = this.state.timeCards.sort((a, b) => {
          const nameA = a[col].toUpperCase(); // ignore upper and lowercase
          const nameB = b[col].toUpperCase(); // ignore upper and lowercase
          if (nameA < nameB) {
              return -1;
          }
          if (nameA > nameB) {
              return 1;
          }
          // names must be equal
          return 0;
      });
      this.setState({timeCards: sortedObj})
  }
  render() {
  return (
    <HashRouter basename="/">
      <Nav />
      <div className="App col-lg-8 mx-auto p-2 py-md-5">
        <Row id='upload'>
            <Card>
                <Card.Body>
                    <Form className="form-inline">

                      <Form.Group as={Row} className="mb-3" controlId="date">
                        <Form.Label column sm={2}>
                          Upload File
                        </Form.Label>
                        <Col sm={10}>
                          <Form.Control type="file" size="md" onChange={(e) => this.showFile(e)} />
                        </Col>
                      </Form.Group>

                      <Row className="mb-3">
                        <Form.Group as={Row} className="col-4" controlId="date">
                          <Form.Label column sm={3}>Date</Form.Label>
                          <Col sm={9}>
                            <Form.Select aria-label="Default select example" onChange={this.handleChange}>
                                    <option value="all">All</option>
                                    {Object.keys(this.state.dates)?.map((desc, descKey) => {
                                        return(
                                            <option key={descKey} value={this.state.dates[desc]}>{this.state.dates[desc]}</option>
                                        )
                                    })}
                            </Form.Select>
                          </Col>
                        </Form.Group>
                        <Form.Group as={Row} className="col-4" controlId="role">
                          <Form.Label column sm={3}>Role</Form.Label>
                          <Col sm={9}>
                            <Form.Select aria-label="Default select example" onChange={this.handleChange}>
                                    <option value="all">All</option>
                                    {Object.keys(this.state.roles)?.map((desc, descKey) => {
                                        return(
                                            <option key={descKey} value={this.state.roles[desc]}>{this.state.roles[desc]}</option>
                                        )
                                    })}
                            </Form.Select>
                          </Col>
                        </Form.Group>
                        <Form.Group as={Row} className="col-4" controlId="name">
                          <Form.Label column sm={3}>Name</Form.Label>

                          <Col sm={9}>
                            <Form.Select aria-label="Default select example" onChange={this.handleChange}>
                                    <option value="all">All</option>
                                    {Object.keys(this.state.names)?.map((desc, descKey) => {
                                        return(
                                            <option key={descKey} value={this.state.names[desc]}>{this.state.names[desc]}</option>
                                        )
                                    })}
                            </Form.Select>
                          </Col>
                        </Form.Group>
                      </Row>
                      <Row className="mb-3">

                        <Form.Group as={Row} className="col-4" controlId="empID">
                          <Form.Label column sm={6}>Employee ID</Form.Label>

                          <Col sm={6}>
                            <Form.Select aria-label="Default select example" onChange={this.handleChange}>
                                    <option value="all">All</option>
                                    {Object.keys(this.state.empIDS)?.map((desc, descKey) => {
                                        return(
                                            <option key={descKey} value={this.state.empIDS[desc]}>{this.state.empIDS[desc]}</option>
                                        )
                                    })}
                            </Form.Select>
                          </Col>
                        </Form.Group>
                        <Form.Group as={Row} className="col-4" controlId="roleType">
                          <Form.Label column sm={6}>Role Type</Form.Label>

                          <Col sm={6}>
                            <Form.Select aria-label="Default select example" onChange={this.handleChange}>
                                    <option value="all">All</option>
                                    {Object.keys(this.state.roleTypes)?.map((desc, descKey) => {
                                        return(
                                            <option key={descKey} value={this.state.roleTypes[desc]}>{this.state.roleTypes[desc]}</option>
                                        )
                                    })}
                            </Form.Select>
                          </Col>
                        </Form.Group>
                      </Row>
                    </Form>
                </Card.Body>
            </Card>
          </Row>
          <br />
          <br />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="labor" element={<Labor timeCards={this.state} roles={this.state}/>} />
          <Route path="payroll" element={<Payroll timeCards={this.state} roles={this.state}/>} />
          <Route path="schedule" element={<Schedule timeCards={this.state} roles={this.state}/>} />
          <Route path="violations" element={<Violations timeCards={this.state} />} />
          <Route path="OT" element={<OT timeCards={this.state} />} />
          <Route path="*"  element={<NotFound timeCards={this.state} />} />
        </Routes>
      </div>
      <div className="col-lg-8 mx-auto p-3 py-md-5">
        <footer className="pt-5 my-5 text-muted border-top">
          Created by Daniel Vasquez   2024
        </footer>
      </div>
    </HashRouter>
  );
  }
}

export default App;
