import React from "react";
import { Row } from "react-bootstrap";
import OTDepartment from "../../components/OTDepartment"
import OTEmployee from "../../components/OTEmployee"
import OTDate from "../../components/OTDate"
import OTDateChart from "../../components/OTDateChart"


// var MyComponent = React.createClass({
//     render: function() {
//         return <LineChart data={chartData} options={chartOptions}/>
//     }
// });
class index extends React.Component {
    render() {
        return (
            <main>
                <Row>
                    <br />
                    <OTDateChart timeCards={this.props.timeCards.timeCards}/>
                    <br />
                    <OTDepartment timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <OTEmployee timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                    <OTDate timeCards={this.props.timeCards.filteredTimeCards}/>
                    <br />
                </Row>
            </main>
        )
    }
}

export default index;