import React from "react";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { CardGroup } from "react-bootstrap";
import { Link } from "react-router-dom";


function Index(props) {
    return (
        <>
        <CardGroup>
            <Card className="m-3">
                <Card.Header> 
                    <Card.Title>Labor Report</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Card.Text>
                        <p>Breakdowns of time entries and breaks, sales, tips</p>
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Link variant="outline-*" className="navbar-brand logo" to="/labor"><Button variant="primary">View</Button></Link>
                </Card.Footer>
            </Card>
            <Card className="m-3">
                <Card.Header>
                    <Card.Title>Payroll Report</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Card.Text>
                        <p></p>
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Link variant="outline-*" className="navbar-brand logo" to="/payroll"><Button variant="primary">View</Button></Link>
                </Card.Footer>
            </Card>
            <Card className="m-3">
                <Card.Header>
                    <Card.Title>Labor Violations Report</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Card.Text>
                        <p>Forgot Clock Out, No Breaks, Late Breaks, and Short Shifts, Long Breaks</p>
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Link variant="outline-*" className="navbar-brand logo" to="/violations"><Button variant="primary">View</Button></Link>
                </Card.Footer>
            </Card>
            <Card className="m-3">
                <Card.Header>
                    <Card.Title>OT Report</Card.Title>
                </Card.Header>
                <Card.Body>
                    <Card.Text>
                        <p>See OT/DT By Department, Employee, and Dates</p>
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <Link variant="outline-*" className="navbar-brand logo" to="/OT"><Button variant="primary">View</Button></Link>
                </Card.Footer>
            </Card>
        </CardGroup>
        </>
    );
}

export default Index;